import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Employee } from '../model/employee';
import { HttpService } from '../service/http.service';

@Component({
  selector: 'app-employeedetails',
  templateUrl: './employeedetails.component.html',
  styleUrls: ['./employeedetails.component.css']
})
export class EmployeedetailsComponent implements OnInit {

  empobj=<Employee>{};
  allCountry:any[]=[];
  
  isHidden:boolean=true;


  constructor(private route:ActivatedRoute, private service:HttpService, private router:Router) { }

  ngOnInit() {
    this.getDataFromUrl();
    this.GetAllCountry();
   
  }

  getDataFromUrl(){
    this.route.paramMap
    .subscribe((param)=>{

      this.getDataFromBackend(param.get("id"));
    })
  }

  getDataFromBackend(id){
    this.service.getEmployeeById(id)
    .subscribe((response)=>{
      // console.log(response)
      
      this.empobj=(<Employee>response)
    })
  }

  OnUpdateEmployee(){
    this.isHidden=false;
  }

  onSubmit(f){
    // console.log(f.value.name);
    this.service.updateEmployee(this.empobj)
    .subscribe((response)=>{
      // console.log(response)
      this.router.navigate(["/home"])
    })
  
  }
  
  GetAllCountry(){
    this.service.getAllCountry()
    .subscribe((response)=>{
      this.allCountry=(<any>response)
    })
  }
}
